.main {
    width: 80%;
   height: auto;
    margin: auto;
    padding-bottom: 5%;
}

.welcome-text {
  width: 100%;
  height: 5%;
  font-family: Butler, serif;
}

.welcome-text h2 {
  font-size: 5vw;
  letter-spacing: -0.2rem;
  position: absolute;
  margin: auto;
}

.main-image {
  width: 100%;
  height: auto;
  display: flex;
  flex-direction: row-reverse;
  margin: auto;
  padding: auto; 
}
.malek-image {
  width: 30%;
 margin-left: 5px;
}
.main-image-text-container {
  width: 70%;
}
.main-image-text {
  width: 100%;
  height: auto;
  display: flex;
  flex-direction: column;
  margin: auto;
  padding: auto; 
  position: relative;
 
}

.text-image {
  max-width: 100%;
  opacity: 0.2;
 
}
.main-image-text :hover {
  opacity: 1.0;
  min-width: 101%;

}
.main-image-text h3 {
  position: absolute;
  padding: 10%;
  font-size: 2vw;
}
.main h3 {
  color: rgb(255, 255, 255);
  font-family: Arial, Helvetica, sans-serif;
  text-shadow: 2px  gray;
  font-style: italic;

}

.text {
  margin: auto;
  font-size: 1.8vw;
}

img {
max-width: 100%;
height: auto;
margin: auto;

}

.section-2{
  width: 100%;
  display: flex;
  flex-direction: row;
}
.section2-image-cointainer{
  width: 30%;
}
.section-2-image{
max-width: 100%;
}
.section2-text-cointainer {
  width: 60%;
}
.section-2 h3{
 width: 100%;
 margin: auto;
 padding: 5%;
 font-size: 1.5vw;
}

.table {
 margin-left: 25px;
 width:  100%;
 display: flex;
 flex-wrap: wrap;
}
.table tr {
 width: 100% ;
}
.table td {
  width: 10%;
  background-color: rgb(9, 94, 179);
  margin: auto;
  white-space:nowrap;
  font-size: 1.5vw;
  text-align: center;
}
.table tr :hover{
  background-color: #e60000;
  width: 101.5%;
  text-align: center;
}
.photo-cridet {
  font-size: .5vw;
}