
.web-container {
    width: 80%;
   height: auto;
    margin: auto;
    padding-bottom: 5%;
}
.web-container h1 {
    color: rgb(49, 122, 194);
    font-family: Arial, Helvetica, sans-serif;
    white-space: nowrap;
    font-size: 3vw;
  }
  .child-container {
      display: flex;
      flex-direction: row;
      margin: auto;
  }
  .iframe{
    max-width: 100%;
    height: 100%;
    opacity: 0.5;
    margin: auto;
    object-fit: contain;
  }
  .child-container :hover{
    opacity: 1.0;
    width: 100%;
  }

  .web-text {
      width: 50%;
      margin: auto;
     
  }
  .web-text h3{
    width: 90%;
    font-size: 1.8vw;
    margin: auto;
    padding: 10px;
  }
  .web-text ul{
    width: 90%;
    margin: auto;
   font-size: 1.5vw;
   list-style-type:none;
}
.meal-sharing{
      width: 25%;
      margin: auto;
  }

  .iframe{
    display: block;       
    background:white;
    border: none;         
    height: 50vh;       
    width: 100%;
}
  .chattie{
      width: 25%;
      margin: auto;
      
  }
  .linktoweb {
    width: 80%;
    display: flex;
    flex-direction: row;
    margin: auto;
    justify-content: space-between;
    margin: auto;
  }
  .linktoweb a{ 
    width: 10%;
    font-size: 1.3vw;
     list-style-type:none;
     text-decoration:none;
     border-radius: 50%;
     background-color:  rgb(9, 94, 179);
     scroll-behavior: smooth;
     font-weight: bold;
     padding: 3px;
     text-align: center;
     white-space: nowrap;
     margin-top: 10px;
  }
  .chattie iframe {
    display: block;       
    background:white;
    border: none;         
    height: 50vh;       
    max-width: 100%;
  }
  .chattie img{
    max-width: 100%;
  }
  .strong {
      font-style: italic;
      font-weight: bold;
  }