.main-app {
  box-sizing:border-box;
  width: 100%;
  height: 100%;
  background-color: rgb(37, 35, 35);
  color: white;
  margin: auto;

}
.header {
  position: relative;
}

.nav{
  width: 100%;
  display: flex;
  margin: auto;
  text-align: center;
  flex-direction: row;
  padding-left: 10%;
  scroll-behavior: smooth;


}

.nav a{
  width: 5%;
  font-size: 1.3vw;
   list-style-type:none;
   text-decoration:none;
   border-radius: 50%;
   background-color:  rgb(9, 94, 179);
   scroll-behavior: smooth;
   font-weight: bold;
   padding: 3px;

}
/* unvisited link */
a:link {
  color:white
}

/* visited link */
a:visited {
  color: white;
}

/* mouse over link */
a:hover {
  color: red;
  font-size: 2vw;

}

.about {
  height: 100vh;
}
.web {
  width: 100%;
  height: 500px;
}
.movie  {
  height: 100vh;
}
@media (prefers-reduced-motion: no-preference) {
  .main-app  {
    animation: App-logo-spin infinite 20s linear;
  }
}
