.movie-countainer  {
    width: 80%;
    height: auto;
    margin: auto;
    padding-bottom: 5%;
 
  }

  .movie-countainer h1 {
    color: rgb(49, 122, 194);
    font-family: Arial, Helvetica, sans-serif;
    white-space: nowrap;
    font-size: 3vw;
  }
  .video-container{
    width: 100%;
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    margin: auto;
 
  }
  .video-container iframe{
    max-width: 100%;
    margin: auto;
    border: solid rgb(49, 122, 194) 2px;
  }

  .movie-text h3{
font-size: 1.5vw;
  }
  .movie-text ul li{
    font-size: 1.5vw;
      }